import { useState, useRef, FC } from 'react';
import { Link, Redirect, useLocation } from 'react-router-dom';
import { useAppSelector, useAppDispatch } from '../../services/hooks'

import { PasswordInput, Input, Button } from '@ya.praktikum/react-developer-burger-ui-components'
import styles from './login.module.css';

import { loginUser } from '../../services/actions/auth';

import { TLocationState, TProfile, TAuth } from '../../types/types';

export const Login: FC = () => {

    const dispatch = useAppDispatch()
    const location = useLocation<TLocationState>()
    const inputRef = useRef(null)

    type TLogin = Pick<TProfile, 'email' | 'password'>

    const [input, setInput] = useState<TLogin>({
        email: '',
        password: ''
    })

    const { isLogin, loginError, resetPasswordSuccess }: TAuth = useAppSelector((state) => state.auth)
    
    //     
    if(isLogin) {
        return <Redirect to={ location?.state?.from || '/' } />
    }

    //  
    const onSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault()
        dispatch(loginUser(input))
    }

    return (
        <div className={styles.wrapper}>
            <form 
                className={styles.form}
                onSubmit={onSubmit}
            >
                <h3 className={styles.title}></h3>
                
                <div className={styles.input}>
                    <Input
                            type={'email'}
                            placeholder={'E-mail'}
                            onChange={e => setInput({...input, [e.target.name]: e.target.value})}
                            value={input.email}
                            name={'email'}
                            error={false}
                            ref={inputRef}
                            icon={'ProfileIcon'}
                            errorText={''}
                            size={'default'}
                    />
                </div>

                <div className={styles.input}>
                    <PasswordInput
                        onChange={e => setInput({...input, [e.target.name]: e.target.value})}
                        value={input.password} 
                        name={'password'}
                    />
                </div>

                <div className={styles.button}>
                    <Button 
                        type="primary" 
                        size="large"
                    >
                        
                    </Button>
                </div>

                {loginError && <div className={styles.error}> email  </div>}
                {resetPasswordSuccess && <div className={styles.success}>  ,    </div>}

                <div className={styles.text}>
                    <p className={styles.label}>   ?</p>
                    <Link 
                        to='/register'
                        className={styles.link}
                    >
                        
                    </Link>
                </div>

                <div className={styles.text}>
                    <p className={styles.label}> ?</p>
                    <Link 
                        to='/forgot-password'
                        className={styles.link}
                    >
                         
                    </Link>
                </div>

            </form>
        </div>
    )
}