import { useState, FC } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { useAppSelector, useAppDispatch } from '../../services/hooks'

import { Input, Button } from '@ya.praktikum/react-developer-burger-ui-components'
import styles from '../login/login.module.css';

import { forgotPassword } from '../../services/actions/auth';

import { TProfile, TAuth } from '../../types/types';

export const ForgotPassword: FC = () => {
    const dispatch = useAppDispatch()

    type TEmail = Pick<TProfile, 'email'>

    const [input, setInput] = useState<TEmail>({
        email: '',
    })

    const { isLogin, forgotPasswordSuccess, forgotPasswordError }: TAuth = useAppSelector((state) => state.auth)

    //  
    const onSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault()
        dispatch(forgotPassword(input.email))
    }

    if(isLogin) {
        return <Redirect to={'/'} />
    }

    if(forgotPasswordSuccess) {
        return (
            <Redirect to='/reset-password' />
        )
    }
        

    return (
        <div className={styles.wrapper}>
            <form 
                className={styles.form}
                onSubmit={onSubmit}
            >
                <h3 className={styles.title}> </h3>
                
                <div className={styles.input}>
                    <Input
                            type={'email'}
                            placeholder={' e-mail'}
                            value={input.email}
                            name={'email'}
                            error={false}
                            errorText={''}
                            size={'default'}
                            onChange={e => setInput({...input, [e.target.name]: e.target.value})}
                        />
                </div>

                <div className={styles.button}>
                    <Button 
                        type="primary" 
                        size="large"
                    >
                        
                    </Button>
                </div>

                {forgotPasswordError && <p className={styles.error}>  </p>}

                <div className={styles.text}>
                    <p className={styles.label}> ?</p>
                    <Link 
                        to='/login'
                        className={styles.link}
                    >
                        
                    </Link>
                </div>

                

            </form>
        </div>
    )
}